.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background-color: #333;
    color: white;
  }
  
  .nav-left {
    font-size: 24px;
    font-weight: bold;
  }
  
  .nav-search {
    flex: 1;
    display: flex;
    justify-content: center;
  }
  
  .nav-search input {
    width: 300px;
    padding: 8px;
    font-size: 16px;
  }
  
  .nav-search button {
    padding: 8px 12px;
    background-color: #007bff;
    color: white;
    border: none;
    cursor: pointer;
  }
  
  .nav-right {
    margin-left: auto;
  }
  